import { inputs } from '../variables/variables';
import { render } from './render';

export function addUser(event, usersList) {
  event.preventDefault();

  const newUser = {
    name: '',
    username: '',
    email: '',
    website: '',
    phone: '',
    address: {
      street: '',
      suite: '',
      city: '',
      zipcode: '',
    },
  };

  inputs.forEach(input => {
    if (input.name === 'street'
      || input.name === 'suite'
      || input.name === 'city'
      || input.name === 'zipcode'
    ) {
      newUser.address[input.name] = input.value;
    } else {
      newUser[input.name] = input.value;
    }
  });
  usersList.push(newUser);
  render(usersList);
}
